@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&family=Open+Sans:wght@300;400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
   /* outline: 2px solid red;*/
 }


body {
    font-family: 'Open Sans';
   
}

.wrapper {
    max-width: 1280px;
    min-height: 1000px;
    background-color: white;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    position: relative;
  }



.header {
    font-family: 'Poppins';
    background-color: #f6ffed;
    width: 100%;
    height: 70px;
    display: flex;
    margin-bottom: 10px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
}


.header_title {
    line-height: 70px;
}

.first_letter {
   color:#00B96B;
}

.logo_block {
    display: flex;
    margin-left: 2%;
    color: black;
    text-decoration: none;


}

.logo_image {
    width: 53px;
    height: 50px;
    margin: 10px;
}

.ant-input {
    font-family: 'Poppins';
    font-size: 16px;
    max-width: 700px;
    display: block;
    align-items: center;
    margin: 0 auto;
}

.ant-table-wrapper .ant-table-tbody >tr >td {
    border-bottom: 1px solid #dbe4dc;
    font-size: 16px;
 
}

.ant-table-thead {
   background-color: rgb(159, 250, 166, 0.3);
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
   
}



.crypto_icon {
    margin-right: 10px;
    vertical-align: middle;
 
}

.negative_percent {
    color: red;
}
.positive_percent {
    color: #00B96B;
}

.main_info_block {
    display: flex;
    justify-content: space-between;
    padding: 0 2%;
}

.popular_block {
width: 330px;
margin-bottom:  30px;
padding: 10px;
}

.wallet_block {
    background-color: #f6ffed;
     border-radius: 3px;
    display: flex;
    width: 230px;
    height: 80px;
    margin-top: 25px;
    padding: 10px;
    transition: box-shadow 0.3s ease;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
}

.wallet_block:hover {
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);
  background-color:  rgba(159, 250, 160, 0.4);

}

.popular_title {
    margin-bottom: 15px;
    font-size: 18px;
    color: gray;

}

.popular_cryptos {
     display: flex;
    justify-content: space-between;
}

.popular_cryptos_item {
width: 90px;
padding: 5px;
border-radius: 3px;
background-color: #f6ffed;
box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
transition: box-shadow 0.3s ease;
}

.popular_cryptos_item:hover {
    background-color: rgba(159, 250, 160, 0.4);
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);

}

.popular_name {
    font-weight: 600;
}

.wallet_image {
 width: 40px;
 margin-right: 20px;

}

.wallet_total {
    display: flex;
   flex-direction: column;
   justify-content: center;
}

.selected_crypto {
    width: 400px;
    box-shadow: 0px 2px 4px 1px rgba(99, 202, 108, 0.7);
    border-radius: 3px;
    padding: 10px 20px 0px;
    align-self: center;
    margin-bottom: 30px;
  }


.selected_title {
    font-size: 22px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 15px;

}

.selected_name {
    margin-right: 10px;
}

.selected_symbol {
    font-size: 20px;
    font-weight: 100;
}
.detailed_block {
    display: flex;
    justify-content: space-between;
}

.graph_block {
    width: 550px;
    height: 450px; 
    margin-right: 1%;;
    
}

.info_table {
    width: 500px;
    font-size: 18px;
    margin-top: 20px;
    margin-left: 2%;
   
}

.modal_buy_title {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 15px;
    
}

.modal_buy_name {
  color: #00b96b;
}

.modal_buy {
    width: 400px;
    position: absolute;
    top: 100px;
    left : 50%;
    transform: translateX(-50%);
    z-index: 2; 
    background-color: white;
    border-radius: 3px;
    padding: 10px 20px 0px;
    align-self: center;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
   }

   .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 1; 
  }

  .close_icon {
   position: absolute;
   left: 365px;
   top: 5px;
    }

   .close_icon:hover {
    color:gray !important;
  }

  .switch_interval {
    display: flex;
    justify-content: space-between;
    width: 200px;
    height: 30px;
    background-color: rgba(197, 250, 192, 0.5);
    padding: 5px;
    border-radius: 5px;
    margin-bottom: 5px;
    margin-left: 65px;
  } 

  .switch_button {
    width: 37px;
    height: 22px;
    text-align: center;
    border-radius: 7px;
    padding: 1px;
  }

  .switch_button.active {
    background-color: white;
    color:#00b96b;
  }


  .modal_wallet {
    width: 800px;
    position: relative;
    top: 100px;
    left : 50%;
    transform: translateX(-50%);
    z-index: 2; 
    background-color: white;
    border-radius: 3px;
    padding: 20px 20px;
    align-self: center;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  }

  .close_wallet_icon {
    position: absolute;
   /* left: 770px;*/
   right: 20px;
    top: 10px;
  }

  .wallet_table {
    background-color: white;
    margin-bottom: 15px;
  }

  .wallet_table .ant-table-thead {
    background-color: rgb(159, 250, 166, 0.3);
    box-shadow: none;
   
}

.modal_wallet_total {
margin-bottom: 10px;
font-size: 16px;

padding-left: 5px;
}

.wallet_result {
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 600;
    padding-left: 5px;
    display: flex;

}

.arrow {
    width: 40px;
    vertical-align: middle;

}

.change_result {
    margin-right: 10px;
 }

.change_parchase,
.change_percent {
    vertical-align: middle;
    padding-top: 5px;
}



/*-------adaptive-----------*/

@media(max-width: 900px) {
    .detailed_block {
        flex-direction: column;
        justify-content: flex-start;
    }

    .modal_wallet {
       width: 90%;
    }


 .recharts-wrapper {
    width: 500px;
 }
 .recharts-surface { 
    width: 90%;
   }

   .graph_block {
    width: 100%;
   
}

}

@media(max-width: 650px) {
    .main_info_block {
        flex-direction: column;
        justify-content: start;
        margin-bottom: 20px;
    }

    .popular_block {
        margin-bottom: 10px;
    }

    .wallet_block  {
        margin-top: 0;
    }


}

@media(max-width: 350px) {
    .popular_block {
        width: 200px;
    }

    .popular_cryptos {
        display: flex;
        flex-direction: column;
       
    }

    .popular_cryptos_item {
        margin-bottom: 5px;
    }

}